body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: black;
}

.App {
  width: 32vw;
  height: 105vh;
  margin: 0 auto;
  border-radius: 40px;
}

.grid-container {
  display: grid;
  grid-template-columns: 8vw 8vw 8vw 8vw;
  grid-template-rows: auto auto auto auto auto auto;
  justify-items: stretch;
}

.zero {
  grid-column-start: 1;
  grid-column-end: 3;
}

.Display {
  height: 12vh;
  width: 60vw;
  background-color: transparent;
  color: white;
  margin-left: -30vw;
  padding-bottom: 2vh;
  padding-top: 5px;
  border: transparent;
  font-size: 90px;
  font-family: "K2D", sans-serif;
  text-align: right;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.Button {
  height: 8vw;
  width: 8vw;
  font-size: 30px;
  font-family: "K2D", sans-serif;
  border-radius: 50%;
  -webkit-transform: scale(.9);
          transform: scale(.9);
  border: none;
  transition: .2s;
}

.Button:hover {
  cursor: pointer;
}

.btn-0 {
  border-radius: 50px;
  width: 16vw;
  margin-left: -0.5vh;
}

.shrink {
  height: 3vw;
  border-radius: 20px;
  font-size: 25px;
}

.link {
  transition: .3s;
  position: fixed;
  top: 0;
  color: #bbb6b6;
  left: 0;
  border-radius: 15px;
  font-size: 20px;
  padding: 5px;
  font-family: "K2D", sans-serif;
}

.link:hover {
  color: white;
  background-color: rgb(54, 50, 50);
}

.digit {
  color: white;
  background-color: #423f3f;
  font-size: 50px;
  text-align: center;
}

.digit:hover {
  background-color: #6e6868;
}

.digit:active {
  background-color: red;

}

.mainOps {
  color: white;
  background-color: red;
  font-size: 50px;
  text-align: center;
}

.mainOps:hover {
  background-color: #f18d8d;
}

.mainOps:active {
  background-color: #6e6868;
}

.additionalOps {
  color: black;
  background-color: silver;
  text-align: center;
}

.additionalOps:hover {
  background-color: orange;
}

.additionalOps:active {
  background-color: #6e6868;
}
